import React from "react";
import { isBrowser } from "react-device-detect";
import { getPost } from "../api";
import { NotificationManager } from "react-notifications"
import { Form, Button, Col } from 'react-bootstrap';

const ContactForm = () => {
  const handleSubmit = async (e) => {
    e.preventDefault();
    console.clear();
    const { subject, name, email, tel, message } = e.target.elements;
    let details = {
      name: name.value.trim(),
      subject: subject.value.trim(),
      email: email.value.trim(),
      tel: tel.value.trim(),
      body: message.value,

    };
    console.log (details);
    //sendMail ();
    getPost ('/mail', { key: 'romanbr87', ...details })
    .then (val => {
      NotificationManager.success('  ') 
      console.log (val)
    })
    .catch (val => {
      NotificationManager.error('  ')
      console.log (val)
    })

    return true;
  };

  const MyForm = ({style}) =>
    <Form onSubmit={handleSubmit} className={`text-right ${isBrowser && 'bg-light'}`} style={style}>
    <h3 className="text-center"> </h3>

    <Form.Group className="mb-2" as={Col}>
      <Form.Control type="text" size='sm' id="subject" name="subject" placeholder="" />
    </Form.Group>
    
    <Form.Group className="mb-2" as={Col}>
      <Form.Control type="text" size='sm' id="name" name="name" placeholder="" />
    </Form.Group>

    <Form.Group className="mb-2" as={Col}>
      <Form.Control type="email" size='sm' id="email" name="email" placeholder=" " required />
    </Form.Group>

    <Form.Group className="mb-2" as={Col}>
      <Form.Control type="tel" size='sm' id="tel" name="tel" placeholder=" "/>
    </Form.Group>

    <Form.Group className="mb-2" as={Col}>
      <Form.Control as="textarea" size='sm' id="message" name="message" rows="5" placeholder="" required />
    </Form.Group>

    <Button variant="primary" type="submit" style={{marginTop: "0"}}></Button>
  </Form>


  return (
    <React.Fragment>
    { isBrowser ? <MyForm style={{padding: '4% 5%', border: '1px solid silver', borderRadius: '5px'}} />:
    <MyForm />} 
    </React.Fragment>
  );
};

export default ContactForm;