import React from "react";
import { ListGroup, ListGroupItem, Modal, Button } from "react-bootstrap";
import { isBrowser } from "react-device-detect";
import "../App.css";
import "../style/style.css";

export default function DataTable(props) {
  const [name, setName] = React.useState("");

  return (
    <ListGroup className="list2">
      {props.data.links.map((e1, i) => (
        <div key={"subCat" + i}>
          <ListGroupItem
            style={{
              fontWeight: "lighter",
              fontSize: "large",
              paddingTop: "3%",
              marginRight: "-1em",
              borderRadius: "0",
              color: "black",
              backgroundColor: "transparent",
              border: "none",
            }}
          >
            {("" + (i + 1)).padStart(2, "0") + ". "}{" "}
            <span style={{ direction: "ltr" }}>{e1.cat}</span>
          </ListGroupItem>

          {props.soryByAtrr(e1.links).map((e2, j) => (
            <ListGroupItem
              key={e2.site_name}
              style={{
                border: "none",
                margin: "0",
                padding: isBrowser ? "0% 4% 0% 0%" : "0% 6% 0% 0%",
              }}
            >
              {("" + (j + 1)).padStart(2, "0") + ". "}
              <a href={e2.link}>{e2.site_name}</a>

              {e2.link2 && <a href={e2.link2}> 2</a>}

              {e2.link3 && <a href={e2.link3}> 3</a>}

              {e2.facebook_link1 && <a href={e2.facebook_link1}> </a>}

              {e2.facebook_link2 && <a href={e2.facebook_link2}> 2</a>}

              {e2.linkedIn_link && <a href={e2.linkedIn_link}></a>}

              {e2.instagram_link && <a href={e2.instagram_link}> </a>}

              <Button
                variant="link"
                style={{ fontWeight: "400", marginTop: '-0.3em' }}
                onClick={() => {
                  props.setName(e2.site_name);
                  props.setShowModal(true);
                }}
              >
                
              </Button>
            </ListGroupItem>
          ))}
        </div>
      ))}
    </ListGroup>
  );
}
